@import url("https://fonts.googleapis.com/css?family=Lato:100,100i,300,300i,400,400i,700,700i,900,900i");

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

* {
  font-family: Lato, serif;
}

.button-active {
  cursor: pointer;
}

.button-active:hover {
  background-color: #f13d7f;
}

.button-active:active {
  position: relative;
  top: 0.5px;
  box-shadow: 0 3px 1px rgba(0, 0, 0, 0.2);
  background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.4));
}

.button-pressed {
  cursor: pointer;
}

.button-pressed:hover {
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.3);
}

.button-pressed:active {
  position: relative;
  top: 0.5px;
  box-shadow: 0 3px 1px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.1));
}

.button-pressed-wtheff,
.footer__copy-wrap-icon-item:active {
  cursor: pointer;
}

.button-pressed-wtheff:active,
.footer__copy-wrap-icon-item:active {
  position: relative;
  top: 0.5px;
}

.col2 {
  width: calc(16.66667% - 30px);
}

.col5 {
  width: calc(41.66667% - 30px);
}

.col3 {
  width: calc(25% - 30px);
}

.col4 {
  width: calc(33.33333% - 30px);
}

.col1 {
  width: calc(8.33333% - 30px);
}

.margin-top {
  margin-top: 50px;
}

.even,
.odd {
  color: #222222;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

.sprite {
  background: url(../img/spritesheet.png) no-repeat;
  display: block;
}

.sprite-Forma-1 {
  width: 32px;
  height: 29px;
  background-position: -5px -5px;
}

.sprite-Forma-1-_1_ {
  width: 40px;
  height: 40px;
  background-position: -5px -44px;
}

.sprite-Forma-1-_2_ {
  width: 47px;
  height: 35px;
  background-position: -5px -94px;
}

.sprite-Forma-1-_3_ {
  width: 45px;
  height: 32px;
  background-position: -5px -139px;
}

.sprite-Forma-1-copy {
  width: 23px;
  height: 22px;
  background-position: -5px -181px;
}

