.revie {
  margin-top: 100px;
  background: #e9e9e9 url(../img/phone.jpg) no-repeat center;
  background-size: auto;
  height: 440px;
}

.revie__wrap {
  max-width: 1140px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.revie__row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

.revie__comment {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(41.66667% - 30px);
  margin-top: 115px;
}

.revie__comment-row {
  display: flex;
}

.revie__comment-avatar {
  min-width: 83px;
  margin-right: 25px;
}

.revie__comment-text {
  word-break: break-all;
  color: rgba(34, 34, 36, 0.67);
  font-size: 16px;
  font-style: italic;
  line-height: 26px;
  text-align: left;
}

.revie__comment-name {
  color: #f16d7f;
  font-size: 16px;
  font-weight: 700;
  line-height: 26px;
  text-align: left;
}

.revie__comment-city {
  color: #222224;
  font-size: 12px;
  font-weight: 300;
  line-height: 14px;
  text-align: left;
}

.revie__sub {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(41.66667% - 30px);
  margin-top: 115px;
}

.revie__sub-text {
  color: rgba(34, 34, 36, 0.67);
  font-size: 14px;
  font-weight: 400;
  line-height: 5px;
  text-align: center;
}

.revie__sub-text h2 {
  color: rgba(34, 34, 36, 0.67);
  font-size: 24px;
  font-weight: 400;
  line-height: 54px;
}

.revie__sub-form {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.revie__sub-input {
  background-color: #e1e1e1;
  box-sizing: border-box;
  border: hidden;
  width: 334px;
  height: 46px;
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
  padding: 0 20px 0 20px;
}

.revie__sub-input ::placeholder {
  color: rgba(34, 34, 36, 0.67);
  font-size: 14px;
  font-weight: 400;
  line-height: 54px;
}

.revie__sub-button {
  background-color: #f16d7f;
  box-sizing: border-box;
  width: 104px;
  height: 46px;
  border-bottom-right-radius: 30px;
  border-top-right-radius: 30px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 46px;
  text-align: center;
  border: none;
}

