.footer__content {
  max-width: 1140px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  margin-top: 110px;
}

.footer__content-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

.footer__about {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(41.66667% - 30px);
  color: #898989;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}

.footer__company {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(16.66667% - 30px);
}

.footer__company h1 {
  color: #f16d7f;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
}

.footer__company a {
  color: #898989;
  font-size: 16px;
  font-weight: 400;
  line-height: 59px;
  text-align: right;
  text-decoration: none;
}

.footer__company a:hover {
  color: #ef5b70;
}

.footer__copy {
  background-color: #e6e6e6;
  height: 80px;
  margin-top: 80px;
}

.footer__copy-wrap {
  max-width: 1140px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  height: 100%;
}

.footer__copy-wrap-text {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  color: #7a7a7a;
  font-size: 16px;
  font-weight: 600;
  line-height: 42px;
}

.footer__copy-wrap-icon {
  display: flex;
}

.footer__copy-wrap-icon-item {
  height: 30px;
  width: 30px;
  margin-right: 5px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #b2b2b2;
  font-size: 16px;
  font-weight: 400;
  line-height: 42px;
  cursor: pointer;
}

.footer__copy-wrap-icon-item:hover {
  background-color: #f16d7f;
  color: #f7f7f8;
}

.slider__background {
  height: 612px;
  background-color: #e8e8e8;
}

.slider {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  width: 100%;
}

.slider__text {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(50% - 30px);
  height: 92px;
  color: #222222;
  font-size: 39px;
  font-weight: 900;
  text-align: left;
  text-transform: uppercase;
  margin-top: 224px;
  line-height: 26px;
  padding-top: 7px;
