.product__accordion .odd:before {
  display: block;
  content: "";
  width: 5px;
  height: 100%;
  background-color: #ef5b70;
  float: left;
  margin-right: 10px;
}

.product__accordion .even:after,
.product__accordion .odd:after {
  display: table;
  content: "";
  clear: both;
}

.product__accordion .active {
  color: #ef5b70;
}

.product__accordion a {
  color: #6f6e6e;
  font-size: 14px;
  font-weight: 400;
  line-height: 33px;
  text-decoration: none;
  margin: 0 0 20px 15px;
}

.product__accordion a:hover {
  color: #ef5b70;
}

.checkbox {
  margin-right: 15px;
}

.checkbox__label {
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  margin-right: 15px;
}

.checkbox__row {
  display: inline-flex;
  flex-wrap: nowrap;
  align-items: center;
}

.product__sort {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(100% - 30px);
  height: 50px;
  background-color: #f3f3f3;
  display: flex;
  padding-left: 10px;
  align-items: center;
}

.product__sort-form {
  color: #6f6e6e;
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  margin-right: 10px;
}

.product__sort-form button {
  width: 65px;
  height: 30px;
  border: 1px solid #ebebeb;
  background: #FFFFFF;
}

.product__sort-form select {
  border: 1px solid #ebebeb;
  height: 30px;
  width: 80px;
}

.product {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.33333% - 30px);
  overflow: hidden;
}

.product img {
  height: 283px;
  width: auto;
}

.pages {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: 261px;
  height: 44px;
  border-radius: 3px;
  border: 1px solid #ebebeb;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 19px;
}

.pages a {
  font-size: 16px;
  font-weight: bold;
  line-height: 26px;
  color: #c4c4c4;
  text-decoration: none;
}

.pages a:hover {
  color: #ef5b70;
}

.products__button {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: 150px;
  height: 44px;
  border-radius: 3px;
  border: 1px solid #ef5b70;
  background-color: #ffffff;
  text-align: center;
  line-height: 44px;
  color: #ef5b70;
  font-size: 16px;
  font-weight: bold;
}

.products-table__wrapper-row {
  display: flex;
  justify-content: space-between;
}

